import React, { useState, useEffect, useContext } from 'react';
import css from './style.module.css';
import { Card, Spinner } from 'react-bootstrap';
import axios from '../../utils/axios.js';
import ElsegchContext from '../../context/ElsegchContext.js';
import validator from 'validator';
import {toast, ToastContainer} from 'react-toastify';

export default function MInfo() {
  const Ectx = useContext(ElsegchContext);
  const [mergejils, setMergejils] = useState([]);
  const [saving, setSaving ]= useState(false);

  // if (Ectx.state.burtgel_Id === null && Ectx.state.email === null)
  //   history.push('/login');

  useEffect(()=>{
    if(Ectx.state.saving && saving)
      toast.success(" .");
  },[saving, Ectx.state.fname, Ectx.state.lname, Ectx.state.utas, Ectx.state.rd])  

  useEffect(()=>{
    let burtgelId = localStorage.getItem("burtgel_Id");
    if(burtgelId){
      Ectx.rememberMe(burtgelId);
    }
    axios.get(`/elsegch/${burtgelId}/mergejil`)
      .then(result=>{
        setMergejils([...result.data.data]);
      }).catch(err=>
        toast.error("  .    .")
       );
  },[])
  const [rd, setRd] = useState('');
  const [fName, setFname] = useState('');
  const [lName, setLname] = useState('');
  const [utas, setUtas] = useState('');
  const [error, setError] = useState({
    ovog: null,
    ner: null,
    rd: null,
    utas: null,
    main: "",
  });
  const handleOvog = (e) => {
    if (validator.isInt(e.target.value)) {
      setError({ ...error, ovog: '    ' });
      return;
    }
    if (validator.isAlpha(e.target.value)) {
      setError({ ...error, ovog: '     ' });

      return;
    }
    setLname(e.target.value);
    setError({ ...error, ovog: '' });
  };

  const handleNer = (e) => {
    if (validator.isInt(e.target.value)) {
      setError({ ...error, ner: '    ' });
      return;
    }
    if (validator.isAlpha(e.target.value)) {
      setError({ ...error, ner: '     ' });

      return;
    }
    setFname(e.target.value);
    setError({ ...error, ner: '' });
  };

  const handleRd = (e) => {
    if (validator.isAlpha(e.target.value)) {
      setError({ ...error, rd: '     ' });
      return;
    }
    if (validator.matches(e.target.value, /[-]{2}[0-9]{8}/gm)) {
      setRd(e.target.value);
      setError({
        ...error,
        rd: '',
      });
      return;
    }
    setError({
      ...error,
      rd: '   2  8   ',
    });
  };

  const handleUtas = (e) => {
    if (e.target.value == '' && utas.length == 1) {
      setUtas('');
      setError({
        ...error,
        utas: null,
      });
      return;
    }
    if (validator.isInt(e.target.value)) {
      setError({
        ...error,
        utas: null,
      });
      setUtas(e.target.value);
      return;
    }
    setError({
      ...error,
      utas: '    ',
    });
    return;
  };

  const handleBurtgel = (e) => {
    e.preventDefault();

    if (
      validator.isEmpty(lName) ||
      validator.isEmpty(fName) ||
      validator.isEmpty(rd) ||
      validator.isEmpty(utas)
    ) {
      setError({
        ...error,
        main: '      ',
      });
      return;
    }
    setError({
      ...error,
      main: '',
    });
    Ectx.insertMyInfo(Ectx.state.burtgel_Id, Ectx.state.email, lName, fName, rd, utas);
    setSaving(true);
  };


  return (
    <div className="container">
      {Ectx.state.fname && Ectx.state.lname && Ectx.state.rd && Ectx.state.utas ? ( 
        <> 
      <Card style={{ backgroundColor: '#444' }} className="mb-5 text-light">
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        />
        <ToastContainer />
        <Card.Header as="h5"> </Card.Header>
        <Card.Title as="p" className="lead alert alert-info p2">
          {' '}
                
            
        </Card.Title>
        <Card.Body>
          {error.main && (
            <div class="alert alert-danger" role="alert">
              {error.main}
            </div>
          )}
          {Ectx.state.error && (
            <div class="alert alert-danger" role="alert">
              {Ectx.state.error}
            </div>
          )}
          <form onSubmit={handleBurtgel} className="form-floating">
            <div className={`row ${css.dd}`}>
              <div className="form-floating mb-3 mt-3 col">
                <input
                  type="text"
                  className="form-control input-lg"
                  id="ovog"
                  placeholder="  "
                  name="ovog"
                  onChange={handleOvog}
                  disabled
                  value={Ectx.state.lname}
                />
                <label htmlFor="ovog" className=" ps-4">
                  
                </label>
                {error.ovog && (
                  <span class="error text-danger">{error.ovog}</span>
                )}
              </div>
              <div className="form-floating mt-3 mb-3 col">
                <input
                  type="text"
                  className="form-control"
                  id="ner"
                  placeholder="  "
                  name="ner"
                  disabled
                  value={Ectx.state.fname}
                  onChange={handleNer}
                />
                <label htmlFor="ner" className=" ps-4">
                  
                </label>
                {error.ner && (
                  <span class="error text-danger">{error.ner}</span>
                )}
              </div>
            </div>
            <div className={`row ${css.dd}`}>
              <div className="form-floating mb-3 mt-3 col">
                <input
                  type="text"
                  className="form-control"
                  id="rd"
                  placeholder="   "
                  name="rd"
                  disabled
                  value={Ectx.state.rd}
                  onChange={handleRd}
                />
                <label htmlFor="rd" className="ps-4">
                   
                </label>
                {error.rd && <span class="error text-danger">{error.rd}</span>}
              </div>
              <div className="form-floating mt-3 mb-3 col">
                <input
                  type="email"
                  className="form-control"
                  id="email"
                  placeholder="-   "
                  disabled
                  name="email"
                  value={Ectx.state.email}
                />
                <label htmlFor="email" className="ps-4">
                  -
                </label>
              </div>
            </div>

            <div className={`row ${css.dd}`}>
              <div className="form-floating mb-3 mt-3 col">
                <input
                  type="text"
                  className="form-control"
                  id="but"
                  placeholder="   "
                  name="but"
                  disabled
                  value={Ectx.state.burtgel_Id}
                />
                <label htmlFor="but" className="ps-4">
                   
                </label>
              </div>
              <div className="form-floating mt-3 mb-3 col">
                <input
                  type="text"
                  className="form-control"
                  id="utas"
                  placeholder="   "
                  name="utas"
                  disabled
                  value={Ectx.state.utas}
                  onChange={handleUtas}
                />
                <label htmlFor="utas" className="ps-4">
                   
                </label>
                {error.utas && (
                  <span class="error text-danger">{error.utas}</span>
                )}
              </div>
            </div>

            <button type="submit" class="btn btn-primary" disabled>
              {Ectx.state.loading ? (
                <Spinner animation="border" variant="dark" />
              ) : (
                ' '
              )}
            </button>
          </form>
        </Card.Body>
      </Card>
     
      {mergejils.length > 0 && (
        <Card style={{ backgroundColor: '#444' }} className="mb-5 text-light">
          <Card.Body>
            <p className='lead alert alert-info p-4 fs-4'>   </p>
          </Card.Body>
        </Card>
      )}
               
      {mergejils &&
                mergejils.map( mergejil => {
                  return (
                    <Card key={mergejil.mergejilId} style={{ backgroundColor: '#444' }} className="mb-5 text-light">
                      <Card.Body>
                        <p className='lead fs-4 pt-2'>
                          {mergejil.Mergejil.name}
                        </p>
                      </Card.Body>
                    </Card>
                  );
                })
             }
      </>
      )
       : 
      (/* medeelel baihgui uyd */
      <>
      <Card style={{ backgroundColor: '#444' }} className="mb-5 text-light">
       <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
       />
       <ToastContainer />
        <Card.Header as="h5"> </Card.Header>
        <Card.Title as="p" className="lead alert alert-info p2">
          {' '}
                
            
        </Card.Title>
        <Card.Body>
          {error.main && (
            <div class="alert alert-danger" role="alert">
              {error.main}
            </div>
          )}
          {Ectx.state.error && (
            <div class="alert alert-danger" role="alert">
              ,  ....
            </div>
          )}
          <form onSubmit={handleBurtgel} className="form-floating">
            <div className={`row ${css.dd}`}>
              <div className="form-floating mb-3 mt-3 col">
                <input
                  type="text"
                  className="form-control input-lg"
                  id="ovog"
                  placeholder="  "
                  name="ovog"
                  onChange={handleOvog}
                  value={lName}
                />
                <label htmlFor="ovog" className=" ps-4">
                  
                </label>
                {error.ovog && (
                  <span class="error text-danger">{error.ovog}</span>
                )}
              </div>
              <div className="form-floating mt-3 mb-3 col">
                <input
                  type="text"
                  className="form-control"
                  id="ner"
                  placeholder="  "
                  name="ner"
                  value={fName}
                  onChange={handleNer}
                />
                <label htmlFor="ner" className=" ps-4">
                  
                </label>
                {error.ner && (
                  <span class="error text-danger">{error.ner}</span>
                )}
              </div>
            </div>
            <div className={`row ${css.dd}`}>
              <div className="form-floating mb-3 mt-3 col">
                <input
                  type="text"
                  className="form-control"
                  id="rd"
                  placeholder="   "
                  name="rd"
                  onChange={handleRd}
                />
                <label htmlFor="rd" className="ps-4">
                   
                </label>
                {error.rd && <span class="error text-danger">{error.rd}</span>}
              </div>
              <div className="form-floating mt-3 mb-3 col">
                <input
                  type="email"
                  className="form-control"
                  id="email"
                  placeholder="-   "
                  disabled
                  name="email"
                  value={Ectx.state.email}
                />
                <label htmlFor="email" className="ps-4">
                  -
                </label>
              </div>
            </div>

            <div className={`row ${css.dd}`}>
              <div className="form-floating mb-3 mt-3 col">
                <input
                  type="text"
                  className="form-control"
                  id="but"
                  placeholder="   "
                  name="but"
                  disabled
                  value={Ectx.state.burtgel_Id}
                />
                <label htmlFor="but" className="ps-4">
                   
                </label>
              </div>
              <div className="form-floating mt-3 mb-3 col">
                <input
                  type="text"
                  className="form-control"
                  id="utas"
                  placeholder="   "
                  name="utas"
                  value={utas}
                  onChange={handleUtas}
                />
                <label htmlFor="utas" className="ps-4">
                   
                </label>
                {error.utas && (
                  <span class="error text-danger">{error.utas}</span>
                )}
              </div>
            </div>

            <button type="submit" class="btn btn-primary">
              {Ectx.state.loading ? (
                <Spinner animation="border" variant="dark" />
              ) : (
                ' '
              )}
            </button>
          </form>
        </Card.Body>
      </Card>
      {mergejils.length > 0 && (
        <Card style={{ backgroundColor: '#444' }} className="mb-5 text-light">
          <Card.Body>
            <p className='lead alert alert-info p-4 fs-4'>   </p>
          </Card.Body>
        </Card>
      )}
               
      {mergejils &&
                mergejils.map( mergejil => {
                  return (
                    <Card key={mergejil.mergejilId} style={{ backgroundColor: '#444' }} className="mb-5 text-light">
                      <Card.Body>
                        <p className='lead fs-4 pt-2'>
                          {mergejil.Mergejil.name}
                        </p>
                      </Card.Body>
                    </Card>
                  );
                })
             }
      </>
      )}
    
    </div>
  );
}
